/* Game.css */

/* Titoli e bottoni base */
button {
  padding: 5px 10px;
  margin: 5px 0;
  cursor: pointer;
}


.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

/* Modal */
.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal.active {
  display: block;
}

.modal-content {
  position: relative;
  background-color: white;
  margin: 10% auto;
  padding: 20px;
  width: 80%;
  max-width: 800px;
  border-radius: 8px;
}

.close {
  position: absolute;
  right: 15px;
  top: 10px;
  font-size: 24px;
  cursor: pointer;
}

.selected-egg-details {
  width: 35%;
  padding: 15px;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.selected-egg-details img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  margin: 10px 0;
}

.selected-egg-details button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
  margin-top: 15px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f5f5f5;
}


/* HOME */
.home-container {
  padding: 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.auth-buttons {
  margin-bottom: 20px;
  display: flex;
  gap: 15px;
}

.button {
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  color: white;
  font-weight: 500;
}

.button-primary {
  background-color: #3b82f6;
}

.button-secondary {
  background-color: #10b981;
}

.button-danger {
  background-color: #ef4444;
}

.button-gray {
  background-color: #6b7280;
}

.section {
  margin-bottom: 30px;
}

.section-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 15px;
}

.dragon-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.dragon-card {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
}

.dragon-image {
  width: 96px;
  height: 96px;
  object-fit: contain;
}

.dragon-info {
  flex-grow: 1;
}

.dragon-name {
  font-weight: bold;
  margin-bottom: 5px;
}

.stats-box {
  padding: 15px;
  border-radius: 8px;
  margin-top: 15px;
}

.stats-item {
  font-weight: bold;
  margin-bottom: 8px;
}

.stats-item:last-child {
  margin-bottom: 0;
}

/* MARKETPLACE */
.marketplace {
  padding: 20px;
  margin: 0 auto;
}

.mystery-box {
  background-color: rgba(23, 23, 23, 0.8);
  border-radius: 8px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  text-align: center;
}

.mystery-box h3 {
  color: #67c1f5;
  font-size: 1.5rem;
  margin: 0;
}

.mystery-box img {
  max-width: 200px;
  height: auto;
  display: block;
  margin: 0 auto;
}

.mystery-box button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.mystery-box button:hover {
  background-color: #45a049;
}

.egg-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  margin-bottom: 10px;
}

.egg-item img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.egg-item input[type="number"] {
  width: 80px;
  padding: 5px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.egg-item button {
  padding: 5px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.egg-item button.sell {
  background-color: #2196F3;
  color: white;
}

.egg-item button.remove {
  background-color: #f44336;
  color: white;
}

.egg-item button.preview {
  background-color: #9c27b0;
  color: white;
}

/* INVENTARIO */
.inventory-container {
  padding: 20px;
}

.inventory-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.inventory-section {
  margin-bottom: 30px;
}

.section-heading {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 15px;
}

/* Message Styles */
.message {
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 4px;
}

/* Egg List Styles */
.egg-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.egg-list-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
}

.egg-image {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.egg-info {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.egg-name {
  font-weight: 500;
}

.egg-count {
  color: #6b7280;
}

.incubate-button {
  background-color: #3b82f6;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.incubate-button:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

/* Incubator List Styles */
.incubator-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.incubator-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
}

.timer {
  color: #4b5563;
  font-weight: 500;
}

.open-button {
  background-color: #10b981;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.open-button:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

/* Dragon List Styles */
.dragon-inventory-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 15px;
}

.dragon-inventory-card {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 15px;
  background-color: #a3a3a3;
}

.dragon-inventory-image {
  width: 200px;
  height: 200px;
  object-fit: contain;
  margin: 0 auto 15px;
  display: block;
}

.dragon-inventory-stats {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.dragon-stat {
  display: flex;
  justify-content: space-between;
  padding: 4px 0;
  border-bottom: 1px solid #e5e7eb;
}

.dragon-stat:last-child {
  border-bottom: none;
}

.stat-label {
  font-weight: 500;
  color: #4b5563;
}

.stat-value {
  color: #1f2937;
}

/* Empty State Styles */
.empty-state {
  text-align: center;
  padding: 30px;
  background-color: #a3a3a3;
  border-radius: 8px;
  color: #6b7280;
}